.form-box {
    width: 500px;
}

form {
    margin-top: 40px;
    border: solid black 1px;
    border-radius: 10px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    padding: 50px;
    background-color: rgb(225, 222, 219);
}

form label {
    margin-right: 10px;
}

form input {
    width: 200px;
    height: 20px;
}

.authentification {
    display: flex;
    flex-direction: column;
    row-gap: 5px;
}

.label-input {
    margin-bottom: 25px;
}

.button-box {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    gap: 30px;
}

button {
    cursor: pointer;
    padding: 5px 15px;
    font-size: 1em;
    color: white;
    border-radius: 10px;
}

.btn-signup {
    background-color: rgb(22, 87, 14)
}

.btn-signin {
    background-color: rgb(134, 25, 114);
}

@media screen and (max-width: 944px) {
    form {
        margin-top: 65px;
    }
}

@media screen and (max-width: 574px) {
    .form-box {
        width: 100%;
    }
}